<template>
  <div id="set">
    <div class="imgUser">
        <img :src="user.imgUlr"/>
    </div>
    <div class="name"><p>{{user.name}}</p></div>
    <div class="nav">
      <div class="navBox check" v-on:click="goConfig()">
        <div class="navText clearfix">
          <div class="imgBox left">
            <img :src="userImgIcon"/>
          </div>
          <div class="left">  </div>
          <div class="cleae"></div>
        </div>
      </div>
      <div class="navBox" v-on:click="goDavise()">
        <div class="navText clearfix">
          <div class="imgBox left">
            <img :src="devisIcon"/>
          </div>
          <div class="left">  </div>
          <div class="cleae"></div>
        </div>
      </div>
      <div class="navBox" v-on:click="goDavise()">
        <div class="navText clearfix">
          <div class="imgBox left">
            <img :src="devisIcon"/>
          </div>
          <div class="left">  </div>
          <div class="cleae"></div>
        </div>
      </div>
      <div class="navBox" v-on:click="goDavise()">
        <div class="navText clearfix">
          <div class="imgBox left">
            <img :src="devisIcon"/>
          </div>
          <div class="left">  </div>
          <div class="cleae"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  #set{padding-top: 85px;max-width: 230px;height: 100%}
 .imgUser{width: 73px;height: 73px;border-radius:40px;box-shadow:0 0 16px #e2e7ea; margin: auto;margin-bottom: 15px;overflow: hidden}
 .imgUser img {max-width: 100%}
 p{text-align: center;font-size: 14px;}
 .nav{ padding-top:70px;}
 .navText{ width: 120px;margin: auto;font-size:14px;color: #656565;line-height: 28px;}
 .navBox{width: 100%;margin-top: 20px;height: 28px;border-left: 4px solid #fff;}
 .check{ width: 100%;margin-top: 20px;height: 28px;border-left: 4px solid #0d71b7; }
 .check .navText{ color: #0d71b7;}
 .imgBox{width: 26px;height:26px;margin-right: 10px;}
</style>
<script>
  import HeaderPublc from './herder.vue'
  import UserImgIcon from '../../../static/img/icon/user.png'
  import DevisIcon from '../../../static/img/icon/devis.png'
  import UserImg from '../../../static/img/icon/userDeFilt.png'
  export default {
    name: 'my-login',
    data () {
      return {
        user: {
          imgUlr: UserImg, // 
          name: ''
        },
        userImgIcon: UserImgIcon,
        devisIcon: DevisIcon,
        remember: false, // 
        error: false, // 
        defaultLayers: null,
        ui: null,
        list: [],
        bubble: null,
        routeInstructionsContainer: null,
        input: null
      }
    },
    methods: {
      login () {
        this.$router.push({ name: "map" })
      },
      goConfig () {
        this.$router.push({ name: 'AdminConfig' })
      },
      goDavise () {
        this.$router.push({ name: 'adminDavise' })
      }
    },
    computed: {

    },
    mounted () {
      // 
      var parm = {logo: false, login: false, us: true, language: true, user: false, nav: false}
      this.$store.commit('updateNave', parm)
    },
    components: {
       HeaderPublc: HeaderPublc
    }
  }
</script>




